import React, { Component } from 'react';
import { Table, Form, Row, Col, Input, Select, Switch, Button, message } from 'antd';
import http from '@/util/request'

const SelectFormCol = {
    labelCol: {
        xs: { span: 24 },
        sm: { span: 8 },
    },
    wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 },
    }
}

const { Option } = Select

const SelectForm = Form.create({
    name: 'SelectForm'
})(
    class SelectFormContent extends Component {
        handleReset = () => {
            this.props.form.resetFields();
        }
        handleSearch = (e) => {
            e.preventDefault();
            this.props.form.validateFields((err, values) => {
                if (!err) {
                    this.props.handleSearch(values);
                }
            });
        }
        render() {
            const { getFieldDecorator } = this.props.form;
            return (
                <Form
                    className="products-search-form"
                    onSubmit={e => this.handleSearch(e)}
                    {...SelectFormCol}
                >
                    <Row gutter={24}>
                        <Col span={8}>
                            <Form.Item label="">
                                {getFieldDecorator(`name`)(
                                    <Input placeholder="" />
                                )}
                            </Form.Item>
                        </Col>
                        <Col span={8} style={{ textAlign: 'right' }}>
                            <Button type="primary" htmlType="submit"></Button>
                            <Button style={{ marginLeft: 8 }} onClick={this.handleReset}>
                                
                        </Button>
                        </Col>
                    </Row>
                </Form>
            )
        }
    }
)

class TableList extends Component {
    state = {
        columns: [
            {
                title: '',
                dataIndex: 'name',
                key: 'name'
            },
            {
                title: '',
                dataIndex: 'description',
                key: 'description'
            },
            {
                title: '',
                dataIndex: 'operation',
                key: 'operation',
                render: (operation, record) => < div >
                    <Button type="primary" onClick={() => this.edit(record)}>Edit</Button>
                </div >
            },
        ],
        list: []
    }

    componentDidMount() {
        this.getList();
    }

    edit = (record) => {
        this.props.handelEditModel(record)
    }

    handleSearch = (values) => {
        this.getList(values);
    }

    getList = (params = {}) => {
        http({
            url: '/sales/categories/list',
            method: 'get',
            params: {
                ...params
            }
        }).then(res => {
            if (res.data && res.data.status === 'OK') {
                this.setState({
                    list: res.data.list
                })
            } else {
                message.error((res.data && res.data.msg) || '')
            }
        }).catch(error => {
            message.error(error.message || '')
        })
    }

    render() {
        return (
            <div>
                <SelectForm handleSearch={this.handleSearch} style={{ marginBottom: '20px' }} />
                <Table columns={this.state.columns} dataSource={this.state.list} rowKey="id" />
            </div>
        );
    }
}

export default TableList;